/* leaderboard */

.modal-teste{
    background-color: transparent;
}

.leaderboard {
    max-width: 490px;
    width: 100%;
    border-radius: 12px;
}

.leaderboard header {
    --start: 15%;

    height: 130px;
    background-image: repeating-radial-gradient(circle at var(--start), transparent 0%, transparent 10%, rgba(54, 89, 219, .33) 10%, rgba(54, 89, 219, .33) 17%), linear-gradient(to right, #5b7cfa, #3659db);
    color: #fff;
    position: relative;
    border-radius: 12px 12px 0 0;
    overflow: hidden;
}

.leaderboard .leaderboard__title {
    position: absolute;
    z-index: 2;
    top: 50%;
    right: calc(var(--start) * .75);
    transform: translateY(-50%);
    text-transform: uppercase;
    margin: 0;
}

.leaderboard span {
    display: block;
}

.leaderboard__title--top {
     font-size: 24px;
     font-weight: 700;
     letter-spacing: 7px;
 }

.leaderboard__title--bottom {
     font-size: 13px;
     font-weight: 500;
     letter-spacing: 4px;
     opacity: .65;
     transform: translateY(-2px);
 }

.leaderboard__icon {
    fill: #fff;
    opacity: .35;
    width: 50px;
    position: absolute;
    top: 50%;
    left: var(--start);
    transform: translate(-50%, -50%);
}

.leaderboard__profiles {
     background-color: #fff;
     border-radius: 0 0 12px 12px;
     padding: 15px 15px 20px;
     display: grid;
     grid-row-gap: 8px;
     row-gap: 8px;
 }

.leaderboard__profile {
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    align-items: center;
    padding: 10px 30px 10px 10px;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 5px 7px -1px rgba(51, 51, 51, 0.23);
    cursor: pointer;
    transition: transform .25s cubic-bezier(.7, .98, .86, .98), box-shadow .25s cubic-bezier(.7, .98, .86, .98);
    background-color: #fff;
}

.leaderboard__profile:hover {
     transform: scale(1.2);
     box-shadow: 0 9px 47px 11px rgba(51, 51, 51, 0.18);
}

.leaderboard__picture {
     max-width: 100%;
     width: 60px;
     border-radius: 50%;
     box-shadow: 0 0 0 5px #36be7f, 0 0 0 22px #f3f4f6;
 }

.leaderboard__name {
     color: #979cb0;
     font-weight: 600;
     font-size: 20px;
     letter-spacing: 0.64px;
     margin-left: 12px;
 }

.leaderboard__value {
    color: #35d8ac;
    font-weight: 700;
    font-size: 34px;
    text-align: right;
}

.leaderboard__value > span {
      opacity: .8;
      font-weight: 600;
      font-size: 13px;
      margin-left: 3px;
}

.leaderboard__value  body {
       margin: 0;
       background-color: #eaeaea;
       display: grid;
       height: 100vh;
       place-items: center;
       font-family: 'Source Sans Pro', sans-serif;
   }

.leaderboard {
    box-shadow: 0 0 40px -10px rgba(0, 0, 0, .4);
}
